.iconsWrap{
    display: inline;
}
.icons{
    list-style: none;
    display:flex;
}
.menu{
    list-style: none;
    display:flex;
    margin:0px;
    padding: 10px;
    left:center;
    background-color:#D8CFC3 ;
    border-radius: 10px;

}
.boxExterior{
    background-color:#264B66 ;
    padding: 6px;
    border-radius: 10px;
    margin-top: 0px;
    margin-bottom: 0px;
    position: fixed;
    top:60px;
}
.menuComponent{
    background-color: #D8CFC3;
    border-style:hidden;
    width: 20vw;
    margin:20px,0;
    margin-inline-start: 5px;
}
.menuComponent :hover{
    background-color: #808080;
}
.menuComponentLogo{
    background-color: #D8CFC3;
    position: fixed;
    right: 10px;
    top:20px;
}
.top{
    height: 80px;
    width: 90%;
    background-color: #ffffff;
    position: fixed;
    top:0px;
    left:30px;
}
.top ul{
    list-style: none;
}